import React, { Component } from 'react'
import { autoCompleteSearch } from '../../../../../actions/moderationActions'
import { FontAwesomeIcon } from '../../../../CommonComponents/AssetsHelper';
import { InputGroup, Form, Button, Dropdown, Badge } from 'react-bootstrap';

class PostTagSearch extends Component {
    state = { searchData: [], searchPost: "", selected: [], loading: false, textSearch: "" }

    onSearchText = (textInput) => {
        this.setState({ loading: true, textSearch: textInput })
        if (textInput?.length > 0) {
            autoCompleteSearch(textInput).then((response) => {
                const { responseData, responseCode } = response.data;
                if (responseCode === 200) {
                    this.setState({ searchData: responseData?.tags || [], loading: false })
                } else {
                    this.setState({ searchData: [], loading: false })
                }
            }); 

        } else {
            this.setState({ searchData: [], loading: false, searchPost: '', textSearch: '' });
        }
    };

    onClickToSearch = (selectSearch, status) => event => {
        if (!status) {
            const { selected } = this.state;
            this.state.selected.push(selectSearch)
            this.setState({ searchPost: selectSearch, selected: selected , textSearch:'', searchData: [] }, () => { this.props.updateFilterPostTags(this.state.searchPost) })
        }
        else {
            this.setState({ searchData: [], searchPost: "", textSearch: "" })
            this.props.removeFilterPostTags(selectSearch)
        }
    }

    componentWillReceiveProps(nextProps) {
        const { filterBody } = nextProps
        if (filterBody.postTags.length == 0 && this.state.searchData.length > 0) this.setState({ searchData: [], searchPost: "", textSearch: "" })
    }

    render() {
        const { searchData, loading, textSearch } = this.state;
        const { postTags , selectedTags} = this.props; 
        return <div className="form-group tag_search mb-3 position-relative">
                <InputGroup id="searchTagElements" className={`spinner-secondary spinner-right position-relarive w-100 mb-3`}>
                    <Form.Control placeholder="Search" className={loading ? `spinner` : `spinner`} value={textSearch} onChange={(e)=>this.onSearchText(e.target.value)} autocomplete="off"/>
                    <InputGroup.Text>
                        <FontAwesomeIcon icon={'magnifying-glass'}/>
                    </InputGroup.Text>
                </InputGroup>
                {(searchData && (searchData.length > 0)) &&
                <Dropdown.Menu show={true} className="w-100 dropdwon-height" style={{ overflowY: 'auto' }}>
                    {searchData.map((sItem, index) => {
                        const isChecked = selectedTags?.includes(sItem) || false
                        return <Dropdown.Item key={index} onClick={this.onClickToSearch(sItem, isChecked)}>{sItem}</Dropdown.Item>
                    })}
                </Dropdown.Menu>}
                <div>
                    <div className="tags-group d-flex align-items-center flex-wrap">
                    {postTags && postTags.map((item, index) => {
                        const isSelected = selectedTags?.includes(item) || false
                        return parseInt(index) < 9 ? <Badge bg='' key={index} className="f-center rounded-pill btn btn-outline-secondary" onClick={this.onClickToSearch(item, isSelected)}>
                                    <span className=' text-ellipsis d-block'>{item}</span>
                                    {isSelected && <Button variant='' size='sm' className="p-0 ms-1" onClick={this.onClickToSearch(item, isSelected)}>
                                        <FontAwesomeIcon icon={'xmark'} iconClass={'me-0 fs-6'}/>
                                    </Button>}
                            </Badge>
                    :null})}
                    </div>
                </div>
            </div>
    }
}

export default PostTagSearch;