import React, { Component } from 'react'
import { connect } from "react-redux";
import parse from 'react-html-parser';
import { GET_NETWORK_ICON , getFlagCode} from '../../../../../utils';
import PostTagSearch from './postTagSearch'
import { updateFilterPostTags, updateFilterSocialFeeds, updateFilterPostType, removeFilterSocialFeeds, removeFilterPostType, removeFilterPostTags, removeAllFilterBody, updateFilterRecommendation, removeFilterRecommendation, removeFilterSentiment, updateFilterSentiment, updateFilterLanguage, removeFilterLanguage } from '../../../../../actions/moderationActions'
import { Accordion, Offcanvas, Button, Image, Form } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../CommonComponents/AssetsHelper';
import { getFlags, getLanguageTags } from '../../../../../actions/loginActions';




const hideLangFilter = (feedList, languageFeeds) => {
    return feedList?.reduce((acc, curr) => {
        if (languageFeeds === curr.id) acc = true;
        return acc;
    }, false);
}

const ReactPostType = ({ value, onClickPostType, moderation }) => {
    const selecteds = Object.keys(moderation?.filterBody).length > 0 && moderation?.filterBody?.postType.length > 0 ? moderation?.filterBody?.postType.filter((fin) => fin == value) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <><input type="checkbox" name="data[postType][]" value="1" className="checkbox-custom feedsLabel md-check moderateFilters postTypeFilter" id="moderationpostType1" onClick={onClickPostType(value, isChecked)} checked={isChecked} /> <span></span></>
}

const ReactRecommendation = ({ value, onClickRecommend, moderation }) => {
    const selecteds = Object.keys(moderation?.filterBody).length > 0 && moderation?.filterBody?.recommendation.length > 0 ? moderation?.filterBody?.recommendation.filter((fin) => fin == value) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <div className='d-flex form-check m-0 ps-0 min-h-100'><input type="checkbox" value="1" className="form-check-input feedsLabel md-check moderateFilters recommendation" id="moderationpostType1" onClick={onClickRecommend(value, isChecked)} checked={isChecked} /></div>
}

const ReactSentiment = ({ value, onClickSentiment, moderation }) => {
    const selecteds = Object.keys(moderation?.filterBody).length > 0 && moderation?.filterBody?.sentiment.length > 0 ? moderation?.filterBody?.sentiment.filter((fin) => fin == value) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <div className='d-flex form-check m-0 ps-0 min-h-100'><input type="checkbox" value="1" className="form-check-input feedsLabel md-check moderateFilters sentiment" id="moderationsentiment1" onClick={onClickSentiment(value, isChecked)} checked={isChecked} /></div>
}

const ReactLanguage = ({ value, onClickLanguage, moderation }) => {
    const selecteds = Object.keys(moderation?.filterBody).length > 0 && moderation?.filterBody?.language.length > 0 ? moderation?.filterBody?.language.filter((fin) => fin == value) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <><input type="checkbox" value="1" className="checkbox-custom feedsLabel md-check moderateFilters language" id="moderationlanguage1" onClick={onClickLanguage(value, isChecked)} checked={isChecked} /> <span></span></>
}


const PostTypeCheck = ({ value, onClickPostType, moderation, label, iconName, iconClassName, iconType, id }) => {
    const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.postType.length > 0 ? moderation.filterBody.postType.filter((fin) => fin == value) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <div className='d-flex align-items-center mb-3 form-label'><Form.Check className="d-flex form-check m-0 ps-0 min-h-100" type="checkbox" value="1" id={id} onChange={onClickPostType(value, isChecked)} checked={isChecked} /> <FontAwesomeIcon type={iconType} icon={iconName} iconClass={iconClassName} /> <span className='ms-2'>{label}</span></div>
}

const OtherInput = ({ value, onClickOther, moderation, label, id }) => {
    const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.other.length > 0 ? moderation.filterBody.other.filter((fin) => fin.key == id) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <div className='d-flex align-items-center mb-3 form-label'><Form.Check className="d-flex form-check m-0 ps-0 min-h-100" type="checkbox" value="1" id={id} onChange={onClickOther(id, value, isChecked)} checked={isChecked} /> <span className='ms-2'>{label}</span></div>
}

class FilterBody extends Component {
    constructor(props) {
        super(props);
        this.state = { socialFeeds: [], postType: [], other: [], feeds: [], isActiveAside: false, showAllLang: false, langs: [], flags: [] }
        this.toogleFilter = this.toogleFilter.bind(this);
    }


    toogleFilter = () => {
        this.props.changeFilterTab(this.props.filterTab === 3 ? 0 : 3)
    };

    onSelectFeeds = (key, value, status) => event => {
        if (!status) this.props.updateFilterSocialFeeds({ key, value })
        else this.props.removeFilterSocialFeeds({ key, value })
    }
    onClickPostType = (value, status) => event => {
        if (!status) this.props.updateFilterPostType(value)
        else this.props.removeFilterPostType(value)
    }
    onClickRecommationType = (value, status) => event => {
        if (!status) this.props.updateFilterRecommendation(value)
        else this.props.removeFilterRecommendation(value)
    }
    onClickSentimentType = (value, status) => event => {
        if (!status) this.props.updateFilterSentiment(value)
        else this.props.removeFilterSentiment(value)
    }
    onClickLanguageType = (value, status) => event => {
        if (!status) this.props.updateFilterLanguage(value)
        else this.props.removeFilterLanguage(value)
    }

    onClickUGC = (key, value, status) => event => {
        if (!status) this.props.updateFilterUgc({ key, value })
        else this.props.removeFilterUgc({ key, value })
    }

    languageTags=()=>{
        const { feeds } = this.props 
        getLanguageTags(localStorage.getItem('ownerId')).then((response) => {
            const langs = response.data;
            const result = [];
            if(langs && langs.length > 0){
                // langs = langs.filter((lang) => {
                //     if(hideLangFilter(feeds, lang.feedId)) return lang;
                // });
                langs.sort((a, b) => b.count - a.count).forEach(lang => {
                    if(lang.language != ""){
                        result.push(lang.language);
                    }
                });
            }
            this.setState({ langs: result });
        }).catch(ex => {});

        getFlags().then((response) => {
            const flags = response.data;
            if(flags && flags.length > 0){
                this.setState({ flags: flags });
            }
        }).catch(ex => {});
    }


    componentDidMount() {
        if (window.feedId && window.feedId != "" && !window.location.href.includes("/notificationId/")) {
            const { feeds } = this.props
            feeds && feeds.map((item, index) => { if (window.feedId == item.id) { this.props.updateFilterSocialFeeds({ key: item.name, value: window.feedId }) } })
        }
        this.languageTags();
    }

    render() {
        const { moderation, feeds, userData, hideFilterBody, showAiUpgradePop, filterTab , userTags} = this.props
        const { showAllLang, langs, flags } = this.state;
        const UserRuleEnableAI = false;
        const UserRuleManualTag = true;
        const selectedTags = moderation?.filterBody?.postTags;
        const trueSense = !userData.user.trueSense;
        const langFilters = !showAllLang ? langs.slice(0, 3) : langs;
        const allTags = [ ...(selectedTags || []), ...(Array.isArray(userTags) ? userTags.filter(tag => !selectedTags?.includes(tag[0])).map(tag => tag[0]) : []) ];
        
        return <Offcanvas show={filterTab===3} placement={'end'} onHide={this.toogleFilter} className="filter_aside filterMed">
            <Offcanvas.Body>
                <div className="f-between p-3 border-bottom">
                    <span className="fs-5 fw-medium">Add Filter</span>
                    <Button variant='link' size='sm' className='p-0 border-0' onClick={this.toogleFilter}>
                        <FontAwesomeIcon icon={'xmark'} />
                    </Button>
                </div>
                {(UserRuleEnableAI || UserRuleManualTag) ?
                    <Accordion className="border-bottom" flush>
                        <Accordion.Item defaultactivekey="0">
                            <Accordion.Header>
                                <span className="text-uppercase fs-8 fw-medium">Post Tags</span>
                            </Accordion.Header>
                            <Accordion.Body className="px-3 py-1">
                                {UserRuleEnableAI && <> <i className='d-block fs-6 mb-2'>Powered by <Image src={baseUrl('svg/icons/trusense.svg')}width={'7.428'} height={'11.033'} fluid /> <b>TruSense</b></i></> }
                                <div id="tag_post_btn" className="collapse show" data-parent="#tagspost">
                                    <PostTagSearch postTags={allTags} {...this.props} selectedTags={selectedTags}/>
                                </div>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                    : null}
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fs-8 fw-medium">Social Feeds</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-3 py-1">
                            <div id="social_feed_btn" className="collapse show" data-parent="#post_type_drop">
                                <div>
                                    {
                                        feeds && feeds.map((item, index) => {
                                            const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.socialFeed.length > 0 ? moderation.filterBody.socialFeed.filter((fin) => fin.value == item.id) : []
                                            const isChecked = selecteds && selecteds.length > 0 ? true : false
                                            return item.status ? <Form.Label key={index} className='d-flex align-items-center mb-3' htmlFor={`moderationfeed${item.id}`}>
                                                <Form.Check className='d-flex form-check m-0 ps-0 min-h-100' type="checkbox" id={`moderationfeed${item.id}`} value={item.id} data-filter={item.filterId} onChange={this.onSelectFeeds(item.name, item.id, isChecked)} checked={isChecked} />
                                                  <FontAwesomeIcon type={item.networkId == 12 ? 'solid' : 'brands'} icon={GET_NETWORK_ICON(item.networkId)} iconClass={'mx-2 fab'} />
                                                  <span className='fil_network'>{item.networkId ==29 ? "SnapUp" : item.networkId ==30 ? "Photo Booth" :  parse(item.name)}</span>
                                            </Form.Label> : ''
                                        })
                                    }

                                </div>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>

                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fw-medium">RECOMMENDATION</span>
                            {trueSense ? <Button variant='' className='p-0 mx-1 link-primary border-0' onClick={() => { hideFilterBody(); showAiUpgradePop(1); }}><small>Upgrade Plan</small></Button> : null}

                        </Accordion.Header>
                        <Accordion.Body className="px-3 py-1">
                            <div id="recommendation_btn" className={`${trueSense ? 'upgrade-overlay' : ''} position-relative collapse show`}>
                                <div className="checkbox-list">
                                    <label className="d-flex align-items-center mb-3 form-label" data-text="Recommendation">
                                        <ReactRecommendation value="1" onClickRecommend={this.onClickRecommationType} {...this.props} />
                                            <FontAwesomeIcon type={'solid'} icon={'circle'} iconClass={'mx-2 text-success fs-6'} />
                                        Recommended
                                    </label>

                                    <label className="d-flex align-items-center mb-3 form-label" data-text="Considerable">
                                        <ReactRecommendation value="2" onClickRecommend={this.onClickRecommationType} moderation={moderation} />
                                        <FontAwesomeIcon type={'solid'} icon={'circle'} iconClass={'mx-2 text-warning fs-6'}/>
                                        Considerable
                                    </label>

                                    <label className="d-flex align-items-center mb-3 form-label" data-text="Not-Recommended">
                                        <ReactRecommendation value="3" onClickRecommend={this.onClickRecommationType} moderation={moderation} />
                                        <FontAwesomeIcon type={'solid'} icon={'circle'} iconClass={'mx-2 text-danger fs-6'}/>
                                        Not Recommended
                                    </label>
                                </div>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>



                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fw-medium">Post Type</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-3 py-1">
                            <PostTypeCheck value="1" onClickPostType={this.onClickPostType} {...this.props} label={`Text Only`} iconType={'regular'} iconName={'t'} iconClassName={'ms-2 text-link fs-6'} id={`text_only`} />
                            <PostTypeCheck value="2,4" onClickPostType={this.onClickPostType} moderation={moderation} label={`With Image`} iconType={'light'} iconName={'image'} iconClassName={'ms-2 text-success fs-6'} id={`image_only`} />
                            <PostTypeCheck value="3,5" onClickPostType={this.onClickPostType} moderation={moderation} label={`With Video`} iconType={'regular'} iconName={'clapperboard-play'} iconClassName={'ms-2 text-danger fs-6'} id={`video_only`} />
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>


                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase flex-grow-1 fw-medium">SENTIMENT</span>
                            {trueSense ? <Button variant='' className='link-primary border-0 mx-1' onClick={() => { hideFilterBody(); showAiUpgradePop(2); }}><small>Upgrade Plan</small></Button> : null}
                        </Accordion.Header>
                        <Accordion.Body className="px-3 py-1">
                            <div id="sentiment_btn" className={`${trueSense ? 'upgrade-overlay' : ''}  position-relative collapse show`} data-parent="#sentiment_drop">
                                <div className="checkbox-list">
                                    <label className="d-flex align-items-center mb-3 form-label" data-text="Positive">
                                        <ReactSentiment value="1" onClickSentiment={this.onClickSentimentType} {...this.props} />
                                        <Image className='img-fluid mx-2' src={baseUrl(`icons/emoji/emoji1.svg`)} height={15} width={15} alt='' />
                                        <span>Strongly Positive</span>
                                    </label>

                                    <label className="d-flex align-items-center mb-3 form-label" data-text="Positive">
                                        <ReactSentiment value="2" onClickSentiment={this.onClickSentimentType} {...this.props} />
                                        <Image className='img-fluid mx-2' src={baseUrl(`icons/emoji/emoji2.svg`)} height={15} width={15} alt='' />
                                        <span>Positive</span>
                                    </label>

                                    <label className="d-flex align-items-center mb-3 form-label" data-text="Neutral">
                                        <ReactSentiment value="3" onClickSentiment={this.onClickSentimentType} moderation={moderation} />
                                        <Image className='img-fluid mx-2' src={baseUrl(`icons/emoji/emoji3.svg`)} height={15} width={15} alt='' />
                                        <span>Neutral</span>
                                    </label>

                                    <label className="d-flex align-items-center mb-3 form-label" data-text="Negative">
                                        <ReactSentiment value="4" onClickSentiment={this.onClickSentimentType} moderation={moderation} />
                                        <Image className='img-fluid mx-2' src={baseUrl(`icons/emoji/emoji4.svg`)} height={15} width={15} alt='' />
                                        <span>Negative</span>
                                    </label>

                                    <label className="d-flex align-items-center mb-3 form-label" data-text="Negative">
                                        <ReactSentiment value="5" onClickSentiment={this.onClickSentimentType} moderation={moderation} />
                                        <Image className='img-fluid mx-2' src={baseUrl(`icons/emoji/emoji5.svg`)} height={15} width={15} alt='' />
                                        <span>Strongly Negative</span>
                                    </label>
                                </div>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>


                {langFilters && langFilters.length > 0 ?
                    <Accordion className="border-bottom" flush>
                        <Accordion.Item defaultactivekey="0">
                            <Accordion.Header>
                                <span className="text-uppercase fs-8 fw-medium">LANGUAGE</span>
                                {trueSense ? <Button variant='' className='link-primary mx-1' onClick={() => { hideFilterBody(); showAiUpgradePop(3); }}><small>Upgrade Plan</small></Button> : null}

                            </Accordion.Header>
                            <Accordion.Body className="px-3 py-1">
                                <div id="language_btn" className={`${trueSense ? 'upgrade-overlay' : ''}  position-relative collapse show`} data-parent="#language_drop">
                                    <div className="pt-4 checkbox-list">
                                        {langFilters.map((item, index) => getFlagCode(item, this.state.flags) ? <label className="d-flex align-items-center mb-3 form-label" data-text={item} key={index}>
                                            <ReactLanguage value={item} onClickLanguage={this.onClickLanguageType} {...this.props} />
                                            <div className={`iti-flag ${getFlagCode(item, this.state.flags)}`}></div> {item}
                                        </label> : null)}
                                        {langs.length > 2 ? <p onClick={() => this.setState({ showAllLang: !showAllLang })} className="btn btn-link font-weight-bold text-dark p-0">{showAllLang ? 'Show less' : 'Show more'}</p> : null}
                                    </div>
                                </div>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion> : ''}

                <div className="d-flex flex-column mx-3 py-2">
                    <Button variant='link' onClick={this.props.removeAllFilterBody} className="">Reset Results</Button>
                </div>
            </Offcanvas.Body>
        </Offcanvas>

    }
}
const mapStateToProps = (state) => {
    return {
        moderation: state.moderation,
        feeds: state.manageFeeds?.feeds,
        userData: state.authenticated.userData,
        userTags: state.authenticated.userTags
    }
}
const mapDispatchToProps = dispatch => {
    return {
        updateFilterPostTags: (data) => dispatch(updateFilterPostTags(data)),
        updateFilterSocialFeeds: (data) => dispatch(updateFilterSocialFeeds(data)),
        updateFilterPostType: (data) => dispatch(updateFilterPostType(data)),
        removeFilterSocialFeeds: (data) => dispatch(removeFilterSocialFeeds(data)),
        removeFilterPostType: (data) => dispatch(removeFilterPostType(data)),
        removeFilterPostTags: (data) => dispatch(removeFilterPostTags(data)),
        removeAllFilterBody: () => dispatch(removeAllFilterBody()),
        updateFilterRecommendation: (data) => dispatch(updateFilterRecommendation(data)),
        removeFilterRecommendation: (data) => dispatch(removeFilterRecommendation(data)),
        updateFilterSentiment: (data) => dispatch(updateFilterSentiment(data)),
        removeFilterSentiment: (data) => dispatch(removeFilterSentiment(data)),
        updateFilterLanguage: (data) => dispatch(updateFilterLanguage(data)),
        removeFilterLanguage: (data) => dispatch(removeFilterLanguage(data))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(FilterBody);